<template>
  <div class="container shifted"><center>
      <h1 class="h1">
        Shopping cart 
      </h1>
        <div class="cartBlock">
          <div v-for="(item, key) in cartDict" v-bind:key="key">
            <div v-if="item > 0">
            <div class="cartProdName">{{productDict[key].Name}}</div>
            <div class="cartProdPrice">{{ethOfWei(productDict[key].Price)}}</div>
            <div class="cartQty">{{item}}</div>
            <div class="cartRemove"><button class="removeCartButton" v-on:click="removeFromCart(key)">Remove</button></div>
            </div>
          </div>
        </div>
        <div v-if="sumOfCart() == 0"> <p>You cart is empty</p> </div>
        <div v-else><button class="block button200" v-on:click="checkoutCart">Checkout</button></div>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, ethOfWei } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'Cart',

  data () {
    return {
      productDict: {},
      cartDict: {},
    }
  },

  beforeMount() {
    var productDictStr = localStorage.getItem('productDict');
    if (productDictStr) {
      this.productDict = JSON.parse(productDictStr);
    } else {
      this.productDict = {};
    }
    console.log('productDict:%o', this.productDict);

    var cartDictStr = localStorage.getItem('cartDict');
    if (cartDictStr) {
      this.cartDict = JSON.parse(cartDictStr);
    } else {
      this.cartDict = {};
    }
    console.log('cartDict:%o', this.cartDict);
  },

  methods: {
    removeFromCart(key) {
      //delete this.cartDict[key]; CAUTION: delete can not update UI immediately, so change logic to set vale to 0
      this.cartDict[key] = 0;
      localStorage.setItem('cartDict', JSON.stringify(this.cartDict));
      console.log('cartDict:%o', this.cartDict);
      alert('remove from shopping cart');
    },

    sumOfCart() {
      var sum = 0;
      for (var key in this.cartDict) {
        sum += this.cartDict[key];
      }
      return sum;
    },

    async checkoutCart(row) {
        this.$router.push({path: '/checkoutCart'});
    },
    ethOfWei(wei) {
      return ethOfWei(wei);
    }
  }
}
</script>

<style>

.cartBlock {
  dispaly:table;
  table-layout: fixed;
  border-radius: 25px;
  background: #dbfef6;
  padding: 20px;
  margin: 20px;
}

.cartProdName {
  display: table-cell;
  text-align: left;
  vertical-align: middle;
  width: 50%;
}

.cartProdPrice {
  display: table-cell;
  text-align: right;
  vertical-align: middle;
}

.cartQty {
  display: table-cell;
  text-align: right;
  vertical-align: middle;
  width: 32px;
}

.cartRemove { 
  display: table-cell;
  text-align: right;
  vertical-align: middle;
  width: 100px;
}



</style>




