<template>
  <div>
  <center>
  <div class="herbcontainer">
  <div v-for="(row, index) in rows" :key='index'>
    <div class="herbblock">
      <img :src=row.ImageUrl width="280px" style="vertical-align:top" v-on:click="detail(row)">
      <div class="productname" v-on:click="detail(row)">{{row.Name}}</div>
      <div class="productbrief" v-on:click="detail(row)">{{row.Brief}}</div>
      <div class="productcontainer">
        <div class="left-half" v-on:click="detail(row)"><i class="fa fa-star" v-show="row.Rating>=1"/><i class="fa fa-star" v-show="row.Rating>=2"/><i class="fa fa-star" v-show="row.Rating>=3"/><i class="fa fa-star" v-show="row.Rating>=4"/><i class="fa fa-star" v-show="row.Rating>=5"/></div>
        <div class="right-half" v-on:click="detail(row)">{{ethOfWei(row.Price)}}</div>
      </div>
      <div class="productbrief" v-on:click="detail(row)">{{row.ReviewCount}} reviews</div>
      <button v-on:click="addToCart(row)">Add to cart</button><button v-on:click="addToWishlist(row)"><i class="fa fa-heart"/></button>
    </div>
   </div>
  </div>
  </center>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, ethOfWei } from '../util/funcs.js';

export default {
  name: 'Home',

  data () {
    return {
      rows: [ ]
    }
  },

  beforeMount() {
    const currentUser = this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListProducts');
    API.post('herbListProducts', '', {}).then(response => {
      console.log('herbListProducts return : %o',  response);
      this.rows = response.Products;
    }).catch(error => {
      console.log(error);
    });

  },

  methods: {
    detail(row) {
      this.$router.push({path: '/product', query:{'productId': row.ProductId}});
    },

    addToCart(row) {
      var productDictStr = localStorage.getItem('productDict');
      var productDict;
      if (productDictStr) {
        productDict = JSON.parse(productDictStr);
      } else {
        productDict = {};
      }
      productDict[row.ProductId] = row;
      localStorage.setItem('productDict', JSON.stringify(productDict));

      var cartDictStr = localStorage.getItem('cartDict');
      var cartDict;
      if (cartDictStr) {
        cartDict = JSON.parse(cartDictStr);
      } else {
        cartDict = {};
      }
      if (row.ProductId in cartDict) {
        cartDict[row.ProductId] = cartDict[row.ProductId] + 1;
      } else {
        cartDict[row.ProductId] = 1;
      }
      localStorage.setItem('cartDict', JSON.stringify(cartDict));
      alert('Product added into shopping cart');
    },
    addToWishlist(row) {
      var wishDictStr= localStorage.getItem('wishDict');
      var wishDict;
      if (wishDictStr) {
        wishDict = JSON.parse(wishDictStr);
      } else {
        wishDict = {};
      } 
      wishDict[row.ProductId] = row;
      localStorage.setItem('wishDict', JSON.stringify(wishDict));
      alert('Product added into wish list');
    },
    ethOfWei(wei) {
      return ethOfWei(wei);
    }
  }
}
</script>



