<template>
  <div class="container shifted"><center>
    <div v-show="rows.length != 0">
      <h1 class="h1">
        My product orders
      </h1>
      <div class="herbcontainer">
        <div v-for="(row, index) in rows" :key='index'>

          <div class="cartBlock">
            <div v-for="(item, key) in row.CartDict" v-bind:key="key">
              <div class="cartProdName">{{row.ProductDict[key].Name}}</div>
              <div class="cartProdPrice">{{ethOfWei(row.ProductDict[key].Price)}}</div>
              <div class="cartQty">{{item}}</div>
            </div>
          </div>


        </div>
      </div>
    </div>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, ethOfWei } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'MyPkgs',

  data () {
    return {
      rows: []
    }
  },

  async beforeMount() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();
    console.log('call herbMyPkgs');
    API.post('herbMyPkgs', '', {}).then(response => {
      console.log('herbMyPkgs return : %o',  response.Pkgs);
      this.rows = [];
      for (var i=0; i<response.Pkgs.length; i++) {
        var pkg = response.Pkgs[i];
        this.rows.push({'CartDict': JSON.parse(pkg.Cart), 'ProductDict': JSON.parse(pkg.Product)});
      }
    }).catch(error => {
      console.log(error);
    });
  },

  methods: {
    async sell(row) {
        this.$router.push({path: '/sell', query:{'serialId': row.SerialId, 'herbId': row.HerbId, 'refPrice': priceOf(row.CreateAt) }});
    },

    ethOfWei(wei) {
      return ethOfWei(wei);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    }
  }
}
</script>



