
<template>
  <div class="nav">
      <span class="navbar-brand">
        <img src="@/assets/png/htc.png" width="50px" align="middle">
      </span>
      <span class="navbar-end">
        <span class="navbar-item">
          <a class="item" v-on:click="leadboards">Leadboards</a>
          <a class="item" v-on:click="auction">Auction</a>
          <a class="item" v-on:click="myPlants">My Plants</a>
          <a id="plantbutton" v-on:click="plant">Plant</a>
          <a v-if="user" class="item" v-on:click="profile">Profile</a>
          <a v-if="user" class="item" v-on:click="myPkgs">History</a>
          <a class="item" v-on:click="products">Products</a>
        </span>
        <span class="navbar-item shopping-cart" v-on:click="checkOut">
          <span class="fa fa-shopping-cart"></span>
        </span>
      </span>
  </div>
</template>

<!--
      <a class="item" v-on:click="products">Products</a>
      <a class="item" v-on:click="referral">Referral</a>
-->

<script>
import { Auth, Hub } from 'aws-amplify';
import AmplifyStore from '../store';


export default {
  name: 'Menu',
  data () {
    return {
    }
  },
  computed: {
    user() { 
      return AmplifyStore.state.user
    },
    //E-Commerce Begin
    numProductsAdded() {
      var productsAdded = localStorage.getItem('productsAdded');
      if (productsAdded == null) {
        return 0;
      }
      return productsAdded.length;
    }
    //E-Commerce End
  },
  methods: {
    leadboards: function() {
        this.$router.push('/')
    },
    plant: function() {
        this.$router.push('/plant')
    },
    auction: function() {
        this.$router.push('/auction')
    },
    myPlants: function() {
        this.$router.push('/myPlants')
    },
    products: function() {
        this.$router.push('/products')
    },
    referral: function() {
        this.$router.push('/referral')
    },
    profile: function() {
        this.$router.push('/profile')
    },
    signOut: function() {
        this.$router.push('/auth/signOut')
    },
    checkOut: function() {
        this.$router.push('/cart')
    },
    myPkgs: function() {
        this.$router.push('/myPkgs')
    }
  }
}
</script>




