<template>
  <div class="container shifted"><center>
      <h1 class="h1">
        Shopping cart 
      </h1>
        <div class="cartBlock">
          <div v-for="(item, key) in cartDict" v-bind:key="key">
            <div v-if="item > 0">
            <div class="cartProdName">{{productDict[key].Name}}</div>
            <div class="cartProdPrice">{{ethOfWei(productDict[key].Price)}}</div>
            <div class="cartQty">{{item}}</div>
            </div>
          </div>
        </div>
        Shipping Information
        <div><input v-model="name" placeholder="Name"></div>
        <div><input v-model="addr" placeholder="Address"></div>
        <div><input v-model="city" placeholder="City"></div>
        <div><input v-model="province" placeholder="Province/State"></div>
        <div><input v-model="country" placeholder="Country"></div>
        <button class="block button200" v-on:click="buy()">Buy</button>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, ethOfWei } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'CheckoutCart',

  data () {
    return {
      product: '',
      productDict: {},
      cart: '',
      cartDict: {},
      name: '',
      addr: '',
      city: '',
      province: '',
      country: ''
    }
  },

  beforeMount() {
    this.product = localStorage.getItem('productDict');
    if (this.product) {
      this.productDict = JSON.parse(this.product);
    } else {
      this.productDict = {};
    }
    console.log('productDict:%o', this.productDict);

    this.cart = localStorage.getItem('cartDict');
    if (this.cart) {
      this.cartDict = JSON.parse(this.cart);
    } else {
      this.cartDict = {};
    }
    console.log('cartDict:%o', this.cartDict);
  },

  methods: {
    async buy() {
      if (this.name.length == 0) {
        alert('Please enter name');
        return;
      }

      if (this.addr.length == 0) {
        alert('Please enter address');
        return;
      }

      if (this.city.length == 0) {
        alert('Please enter city');
        return;
      }

      if (this.province.length == 0) {
        alert('Please enter province');
        return;
      }

      if (this.country.length == 0) {
        alert('Please enter country');
        return;
      }

      const currentUser = await this.$Amplify.Auth.currentUserInfo();

      API.post('herbNewCart', '', {body: {'CartId': uuidv4(), 'AccName':currentUser.username, 'Cart': this.cart, 'Product': this.product, 'ShipName': this.name, 'ShipAddr': this.addr, 'ShipCity': this.city, 'ShipProvince': this.province, 'ShipCountry': this.country }  }).then(response => {
        if (response.statusCode != 200) {
          alert('something went wrong');
          return;
        }
        localStorage.removeItem('cartDict');
        this.$router.push('/products');
      }).catch(error => {
        alert('something went wrong');
        console.log(error)
      });
    },
    ethOfWei(wei) {
      return ethOfWei(wei);
    }
  }
}
</script>

<style>

.cartBlock {
  dispaly:table;
  table-layout: fixed;
  border-radius: 25px;
  background: #dbfef6;
  padding: 20px;
  margin: 20px;
}

.cartProdName {
  display: table-cell;
  text-align: left;
  vertical-align: middle;
  width: 50%;
}

.cartProdPrice {
  display: table-cell;
  text-align: right;
  vertical-align: middle;
}

.cartQty {
  display: table-cell;
  text-align: right;
  vertical-align: middle;
  width: 32px;
}

.cartRemove { 
  display: table-cell;
  text-align: right;
  vertical-align: middle;
  width: 100px;
}



</style>




